      initialProps = goldpage_args.initialProps,
      CONTAINER_ID = goldpage_args.CONTAINER_ID;
  var element = React.createElement(page.view, initialProps);
  var container = document.getElementById(CONTAINER_ID);
  ReactDOM.render(element, container);
